.home-page {
  /*MAIN ANNIMATION SECTION*/
  /*END MAIN ANNIMATION SECTION*/
  font-family: lato;
}

.home-page .main-annimation {
  height: 50vw;
  padding: 25px 0;
  display: table;
  width: 100%;
}

.home-page .main-annimation .left {
  display: table-cell;
  width: 50vw;
  height: 100%;
}

.home-page .main-annimation .left .maps-container {
  position: relative;
}

.home-page .main-annimation .left .maps-container .africa-shadow {
  position: absolute;
  top: 30px;
  left: 30px;
  z-index: -2;
}

.home-page .main-annimation .right {
  display: table-cell;
  width: 50vw;
  height: 100%;
}

.home-page .main-annimation .right .annim {
  height: 100%;
  position: absolute;
  right: 0;
  background: url("/img/annimation/right-1.png") no-repeat;
  background-size: contain;
  background-position: right;
}

.home-page .main-annimation .right .annim .anim-text {
  background: white;
  margin: 0 auto;
  display: block;
  width: 50%;
  position: relative;
  top: 230px;
}

.home-page .video-conainer {
  width: 100%;
}

.home-page .video-conainer video {
  width: 100%;
}

.home-page .video-conainer .text-video {
  position: absolute;
  top: 30vw;
  padding: 0 8%;
  margin: 0 auto;
  color: white;
}

@media (max-width: 767px) {
  .home-page .video-conainer .text-video {
    position: absolute;
    top: 10vw;
  }
}

.home-page .video-conainer .text-video .right-text-video {
  position: absolute;
  bottom: 0;
  right: 50px;
}

@media (max-width: 767px) {
  .home-page .video-conainer .text-video .right-text-video {
    position: relative;
  }
}

.home-page .video-conainer .text-video .right-text-video .goal-container {
  width: 100%;
  height: 15px;
  border-radius: 15px;
  border: solid 1px white;
}

.home-page .video-conainer .text-video .right-text-video .goal-container .goal-bar {
  max-width: 100%;
  width: 0%;
  background: white;
  height: 100%;
  border-radius: 15px;
}

.home-page .video-conainer .text-video .right-text-video .goal-container .goal-button {
  border: 1px solid white;
  background: transparent;
  color: white;
  float: right;
  margin-top: 20px;
}

@media (max-width: 767px) {
  .home-page .video-conainer .text-video .right-text-video .goal-container .goal-button {
    float: none;
    margin: 20px auto;
    width: 60%;
    display: block;
  }
}

.home-page .video-conainer .text-video .right-text-video .goal-container .goal-button:hover {
  background: rgba(173, 173, 173, 0.6);
}

.home-page .para {
  color: blue;
}

@media (max-width: 767px) {
  .home-page .para {
    color: green;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .home-page .para {
    color: red;
  }
}
